@import url(https://fonts.googleapis.com/css2?family=Roboto);
* {
   padding: 0;
   margin: 0;
   box-sizing: border-box;
   font-family: "Roboto", sans-serif;
}

:root {
   --primary-red: #dd0611;
   --form-background-dark: #333333;
}

/* Reusable styling */

.header {
   width: 100%;
   height: 100px;
   position: absolute;
   display: flex;
   align-items: center;
   justify-content: space-between;
   padding-right: 100px;
   margin-left: 40px;
}

.top-logo {
   display: flex;
   z-index: 100;
   width: 210px;
   top: 0;
   left: 0;
}

.overlay {
   position: absolute;
   background-color: rgba(0, 0, 0, 0.7);
   width: 100%;
   height: 100%;
   top: 0;
   right: 0;
   z-index: 1;
}

/* Forms */

.form-center {
   display: flex;
   flex-direction: column;
   position: relative;
   width: 500px;
   height: 700px;
   background-color: rgba(0, 0, 0, 0.7);
   padding: 60px 68px 40px;
   color: white;
   margin: 0 auto;
   z-index: 100;
   margin-bottom: 90px;
   top: 50%;
   transform: translateY(-50%);
}

.form-center h1 {
   margin-bottom: 30px;
   font-size: 2.5rem;
}

.form-center input {
   padding: 16px 20px;
   background-color: #333333;
   background-color: var(--form-background-dark);
   color: white;
   border: none;
   border-radius: 5px;
   font-size: 1rem;
}

.form-center input:not(:last-child) {
   margin-bottom: 20px;
}

.form-center .btn {
   width: 100%;
   font-weight: 600;
   font-size: 1.1rem;
   margin-bottom: 30px;
}

.form-center p,
.form-center b {
   color: white;
   margin-bottom: 10px;
}

.form-center a {
   color: white;
   text-decoration: none;
}

/* End of Forms */

.landing-page-hero {
   width: 100%;
   height: 750px;
   background: url("https://assets.nflxext.com/ffe/siteui/vlv3/b6f906ab-4f9f-49b5-a63b-6a3b29c8d04b/22f47234-bd61-4b6a-8ac2-fcd6e587aa4b/US-en-20210118-popsignuptwoweeks-perspective_alpha_website_small.jpg");
}

.landing-page-header {
   position: absolute;
   display: flex;
   width: 100%;
   justify-content: space-between;
   align-items: center;
   height: 100px;
   padding-right: 100px;
   margin-left: 40px;
   z-index: 10;
}

.hero-content {
   display: flex;
   flex-direction: column;
   align-items: center;
   justify-content: center;
   height: 100%;
   z-index: 10;
   position: relative;
}

.hero-content form input {
   margin-top: 20px;
}

.hero-content form input {
   width: 550px;
   padding: 20px;
   font-size: 1rem;
}

.hero-content form button {
   border: none;
   padding: 20px 100px;
   color: white;
   font-size: 1rem;
   background-color: var(--primary-red);
}

.hero-content h1 {
   font-size: 5rem;
   text-align: center;
   letter-spacing: 1px;
}

.hero-content h3 {
   letter-spacing: 1px;
   font-size: 1.7rem;
   margin-top: 20px;
   font-weight: 400;
}

.hero-content h4 {
   font-size: 1.2rem;
   margin-top: 20px;
   font-weight: 100;
   letter-spacing: 1px;
}

/* Logo */
.landing-page-logo {
   top: 0;
   left: 0;
}

.landing-page-logo img {
   width: 210px;
}

.landing-page-button-login {
   right: 0;
}

.our-story-page {
   position: relative;
   border-bottom: 8px solid rgb(143, 131, 111);
   margin-bottom: 0px;
   color: white;
   padding: 75px 45px;
   background-color: black;
}

.landing-page-tv {
   display: flex;
   justify-content: center;
   align-items: center;
}

.content-text {
   display: flex;
   justify-content: center;
   flex-direction: column;
   width: 500px;
}

.tv-image img {
   width: 500px;
}

/* Login page */

.login-page {
   background: url("https://assets.nflxext.com/ffe/siteui/vlv3/b6f906ab-4f9f-49b5-a63b-6a3b29c8d04b/22f47234-bd61-4b6a-8ac2-fcd6e587aa4b/US-en-20210118-popsignuptwoweeks-perspective_alpha_website_small.jpg");
   width: 100vw;
   height: 100vh;
   z-index: -1;
   overflow: hidden;
}

.login-page .overlay {
   background-color: rgba(0, 0, 0, 0.5);
}

/* End of Login page */

/* Button Styling */
.btn {
   border: none;
   outline: none;
   font-size: 14px;
   padding: 10px 15px;
   border-radius: 5px;
   background-color: var(--primary-red);
   color: #fff;
   cursor: pointer;
}

